.main{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.container{
    width: 95%;
    height: 95%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,.6);
    color: white;
    margin: auto;
    border-radius: 1rem;
    text-align: center;
}
.container * + * {
    margin-top: 1rem;
}
.error{
    color: black;
    background: rgba(255,0,0,0.7);
    padding: .3rem 1rem;
}
.explanation{
    font-style: italic;
    width: 50%;
}
form{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

input{
    outline: none;
    padding: .5rem 1rem;
    border: none;
    background-color: rgba(0,0,0,.3);
    width: 50%;
    color: white;
    border-radius: .3rem;
}

input::placeholder{
    color: rgba(255,255,255,.5);
}

button{
    outline: none;
    padding: .5rem 1rem;
    color: white;
    background-color: var(--clr-primary);
    border: none;
    border-radius: .3rem;
}
button:active{
    background-color: var(--clr-secondary);
}